                         </div>
                     </div>
                  </div>
                 <nav class=" nav boder4">
                     <ul class="nav nav-tabs" style="padding: 0;margin: 0">
                       <li role="presentation" v-for="list in docterAndExpert" :class="{'active':list.check}" v-on:click="getList(list)"><a href="javascript:;"> {{list.title}}{{list.number}}</a></li>
                     </ul>
                </nav>
              <!---->
              <div class="card" style="margin-top: 30px;">
                <div class=''>
                  <div class='P-content-bottom'>
                    <table class="table table-bordered">
                      <thead>
                      <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th><span v-show="fromDat.userType==2"></span><span v-show="fromDat.userType==1"></span></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                      </tr>
                      </thead>
                      <tbody>
                      <tr v-for="list in tableList">
                        <td>{{list.chineseName}}</td>
                        <td>{{list.euMobile}}</td>
                        <td><span v-show="list.sex==1"></span> <span v-show="list.sex==0"></span></td>
                        <td>{{list.title}}</td>
                        <td>{{list.hospitalName}}</td>
                        <td><span v-show="fromDat.userType==2 && !list.isAdmin">  </span> <span v-show="fromDat.userType==1 && !list.isAdmin">  </span><span v-show="list.isAdmin">  </span></td>
                        <td><span v-show="list.active==1" ></span><span v-show="list.active==0" ></span><span v-show="list.active==2" > </span></td>
                        <td><p>{{list.updateUser}}</p></td>
                        <td>{{list.updateWhen}}</td>
                        <td>
                          <button type="button" class="btn btn-primary btn-sm details-btn" @click="goCheart(list,'Cheart')"><i class="fa fa-clone"></i> </button>
                          <button type="button" v-show="list.active==0 ||list.active==2" class="btn btn-success  btn-sm details-btn" data-toggle="modal" data-target="#myModal"
                                  @click="getStatus(list,1,list.usersId)"><i class="fa fa-check"></i> </button>
                          <button type="button" v-show="list.active==1" class="btn btn-warning btn-sm details-btn " data-toggle="modal" data-target="#myModal"
                                  @click="getStatus(list,2,list.usersId)"><i class="fa fa-lock" ></i> </button>
                          <button type="button" v-show="list.active==1 ||list.active==2" class="btn btn-danger btn-sm details-btn" data-toggle="modal" data-target="#myModal"
                                  @click="getStatus(list,0,list.usersId)"><i class="fa fa-stop"></i> </button>
                        </td>
                      </tr>
                      <tr v-if="tableList == null || tableList.length == 0">
                        <td colspan="10">
                          <span></span>
                        </td>
                      </tr>
                      </tbody>
                    </table>
                    <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
                      <div class="modal-dialog" role="document">
                        <div class="modal-content">
                          <div class="modal-header">
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                            <h4 class="modal-title" id="myModalLabel" >{{fromDat.userType==1?'':''}}{{titleText}}</h4>
                          </div>
                          <div class="modal-body">
                            <p style="font-size: 12px;" > {{oderListText}}</p>
                          </div>
                          <div class="modal-footer">
                            <button type="button" class="btn default" data-dismiss="modal"> </button>
                            <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="posActive(posActiveParm.id,posActiveParm.active)" ></button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class='flipBtn'>
                <!--<button type="button" @click='previousPage'class="btn btn-default"></button>
               <button type="button" @click='nextPage' class="btn btn-default"></button>moneyBody-->
                <div class="btn-group pull-right" role="group" style='margin-left:10px;' aria-label="...">
                  <button type="button" class="btn btn-default" @click='homePage'></button>
                  <button type="button" class="btn btn-default" @click='previousPage'></button>
                  <button type="button" class="btn btn-default">{{(page.pageNum>0&&page.pageNum<=page.lastPage)?page.pageNum:0}}/{{page.lastPage}}</button>
                  <button type="button" class="btn btn-default" @click='nextPage'></button>
                  <button type="button" class="btn btn-default" @click='lastPage'></button>
                </div>
                <div class="btn-group pull-right" role="group" aria-label="...">
                  <button type="button" class="btn btn-default">{{page.pageSize}}</button>
                  <button type="button" class="btn btn-default">{{page.lastPage}}</button>
                </div>
              </div>
            </div>
        </div>
    </div>
</template>
<style scoped>
  .selected{background-color: #e4e8f1;color: #000}
  .el-select-dropdown__item.selected.hover{
    background-color: #e4e8f1
  }
  #text{
    width:150px;
    height:35px;
    border-radius:5px;
  }
  .flipBtn{
    text-align:center;
  }
  .P-content{
    min-width:960px;
  }
  /**/
  .P-content .P-content-top{
    height:20px;
  }
  /**/
  .P-content-bottom table th,.P-content-bottom table{
    text-align:center;
  }
  .P-content-bottom .table thead{
    background:#f5f5f5;
  }
  /*table*/
  .P-content-bottom .table tr{
    text-align:center;
