  .topSerch{padding-top: 20px;padding-bottom: 20px;}
</style>
<script>
    import CommonHeader from '../public/header.vue';
    import Set from '../public/set.vue';
    import moment from 'moment';
    import $ from 'jquery';
    import publicFn from '../../untils/Public.js';
    // 
    import {RegionPicker} from 'vue-region-picker'
    import REGION_DATA from 'china-area-data'
    RegionPicker.region = REGION_DATA;
    RegionPicker.vueVersion=2;
    export default {
      name: "docterAndExpert",
      data:function() {
        return{
          token:localStorage.getItem('access_token'),
          //
          province:[],
          options1:[],
          options2:[],
          optionSix:[
            {value:-1,label:""},
            {value:1,label:""},
            {value:0,label:""}
          ],
          //
          optionTitle:[
            {value:0,label:""},
            {value:1,label:""},
            {value:2,label:""},
            {value:3,label:""},
            {value:4,label:""}
          ],
          //
          optionStatus:[
            {value:-1,label:""},
            {value:1,label:""},
            {value:0,label:""},
            {value:2,label:""},
          ],
          //
          hospital:[],
          //
          titleText:"",
          //page
          page:{
            "pageNum" : 1,
            "pageSize" :10,
            "size" : null,
            "total" : null,
            "pages" : null,
            "prePage" : null,
            "nextPage" : null,
            "firstPage" : null,
            "lastPage" : null,
          },
          //
          fromDat:{
            province:null,
            city:null,
            area:null,
            sex:-1,
            title:"",
            search:null,
            status:-1,
            userType:1,
            pageNumber:1,
            pageSize:10,
            hospitalId:0
          },
          //
          docterAndExpert:[
            {title:"",id:1,check:true},
            {title:"",id:2,check:false}
            ],
          //
          tableList:null,
          //
          oderListText:" ",
          tableNav:null
        }
      },
      mounted(){
        this.city("86","province");
        this.docterAndExpertList();
        this.hospitalList();
      },
      methods:{
        //goCheart
        goCheart(list,parm){
          if(parm=="Cheart"){
              this.$router.push({name: "addExpert",query: { id:list.usersId,userType:this.fromDat.userType,userid:list.id}});
          }
        },
        //
        city(key,list){
          var dat=REGION_DATA;
          if(list=="province"){
            this.province=[];
            this.options1=[];
            this.options2=[];
            this.fromDat.province="";
            this.fromDat.city="";
            this.fromDat.county="";
          }
          if(list=="city"){
            this.options1=[];
            this.options2=[];
            this.fromDat.city="";
            this.fromDat.county="";
          }
          if(list=="county"){
            this.options2=[];
            this.fromDat.county="";
          }
          var option1=dat[key];
          for (var i in option1){
            if(list=="province"){
              this.province.push({value:i,label:option1[i]});
            }
            if(list=="city"){
              this.options1.push({value:i,label:option1[i]});
            }
            if(list=="county"){
              this.options2.push({value:i,label:option1[i]});
            }
          }
        },
        //chegn
        change(key,list){
          this.city(key,list)
        },
        //table
        getList(list){
          for(let i in this.docterAndExpert){
            this.docterAndExpert[i].check=false
          }
          list.check=true;
          this.fromDat.userType=list.id;
          this.page.pageNum=1;
          this.docterAndExpertList();
        },
        //
        goAddPage(){
          this.$router.push({name:'addExpert',query:{ userType:this.fromDat.userType}});
        },
        //
        docterAndExpertList(currentPage,a){
          if(currentPage) {
            this.page.pageNum = currentPage;
            if(a){
              var parm={};
              if(this.fromDat.search){
                parm.search=this.fromDat.search
              }
              if(this.fromDat.status>=0){
                parm.status=this.fromDat.status
              }
              if(this.fromDat.title && this.fromDat.title!=""){
                parm.title=this.fromDat.title
              };
              if(this.fromDat.hospitalId){
                parm.hospitalId=this.fromDat.hospitalId
              }
              this.navNubmer(parm)
            }
          }
          var parm={
            userType:this.fromDat.userType,
            pageNumber:this.page.pageNum,
            pageSize:this.page.pageSize,
          };
          var dat=REGION_DATA;
          if(this.fromDat.province){
            var key=this.fromDat.province;
            parm.province=dat["86"];
            parm.province=parm.province[key];
          }
          if(this.fromDat.city){
            parm.city=dat[this.fromDat.province];
            var key=this.fromDat.city;
            parm.city=parm.city[key];
          }
          if(this.fromDat.area){
            parm.area=dat[this.fromDat.city];
            var key=this.fromDat.area;
            parm.area=parm.area[key];
          }
          if(this.fromDat.sex>=0){
            parm.sex=this.fromDat.sex
          }
          if(this.fromDat.search){
            parm.search=this.fromDat.search
          }
          if(this.fromDat.status>=0){
            parm.status=this.fromDat.status
          }
          if(this.fromDat.title && this.fromDat.title!=""){
            parm.title=this.fromDat.title
          };
          if(this.fromDat.hospitalId){
            parm.hospitalId=this.fromDat.hospitalId
          }
          this.tableList=[];

          var url="/api/organization/employees/list";
          this.$http.get(url,{
            headers:{
              Authorization: 'bearer '+this.token
            },
            params:parm
          }).then(function(res){
            var pages=res.body;
            this.page={
                "pageNum" : pages.pageNum,
                "pageSize" :pages.pageSize,
                "size" : pages.size,
                "total" : pages.total,
                "pages" : pages.pages,
                "prePage" : pages.prePage,
                "nextPage" : pages.nextPage,
                "firstPage" : pages.firstPage,
                "lastPage" : pages.lastPage,
            }
            var parm=res.body.list;
            for( let i in parm){
              parm[i].updateWhen =publicFn.publicLo.timeCheng(parm[i].updateWhen);
              parm[i].updateWhen = parm[i].updateWhen .times
            }
            this.tableList=parm;
            this.bot=true
          }).catch(function () {
            this.bot=true
          })
        },
        //
        posActive(id,a){
          if(!this.bot){
            this.warning("");
            return false
          }
          var url="/api/organization/employees/active";
          this.bot=false;
          var parmes={
            id:parseInt(id),
            active:parseInt(a)
          }
          parmes=JSON.stringify(parmes);
          this.$http.put(url,parmes,{
            headers:{
              Authorization:'bearer' + this.token
            },
            emulateJSON:true
          }).then(function(res){
            res =res.body;
            this.success("");
            var that=this;
          setTimeout(function () {
            that.docterAndExpertList(1);
          },1000);
          }).catch(function(res){
            this.warning(res.body.message)
          })
        },
        //
        getStatus(list,a,userId){
          var parm,text,textNO,statusText;
          if(a==0){
            statusText=""
          }
          if(a==1){
            statusText=""
          }
          if(a==2){
            statusText=""
          }
          this.posActiveParm={
            id:parseInt(list.id),
            active:parseInt(a)
          }
          if(this.fromDat.userType==2){
            //parm
              parm={
                expertId:userId,
                state:"1,2",
             }
             text="  " +list.chineseName+"  "+statusText+"?";
          }
          else {
            //parm
            parm={
              doctorId:userId,
              state:"1,2",
            };
            text="  " + list.chineseName + "  "+statusText+"?"
          }
          // 
          this.getWorking(parm,text,list.chineseName,statusText);
        },
        //
        getWorking(parm,text,name,title){
          this.oderListSize = 0 ;
           var url="/api/assist-order/assistOrders/web";
           this.$http.get(url,{
             headers:{
               Authorization: 'bearer '+this.token
             },
             params:parm
           }).then(function(res){
             this.titleText=title
              if(res.body.list[0]){
                 this.oderListText=name+"?";
              }else {
                 this.oderListText=text;
              }
           });
        },
        //
        hospitalList () {
          var url="/api/organization/hospital/listall";
          this.$http.get(url,{
            headers:{
              Authorization: 'bearer '+this.token
            }
          }).then(function(res){
            res=res.body;
            var potion=[];
            for(let i in res){
              potion.push({value:res[i].id,label:res[i].name})
            }
            potion.unshift({value:0,label:"/"});
            this.hospital=potion
          }).catch(function (e) {
          });
        },
        //
        homePage(){
          this.page.pageNum=1;
          this.docterAndExpertList(this.page.pageNum);
        },
        //
        lastPage(){
          this.page.pageNum= this.page.lastPage;
          this.docterAndExpertList(this.page.pageNum);
        },
        //
        previousPage(){
          if(this.page.pageNum-1<1){
            this.warning("")
          } else {
            this.page.pageNum--;
            this.docterAndExpertList(this.page.pageNum);
          }
        },
        //
        nextPage(){
            if(this.page.lastPage<this.page.pageNum+1){
              this.warning("")
            } else {
            this.page.pageNum++;
            this.docterAndExpertList(this.page.pageNum);
          }
        },
        open:function(text) {
          this.$message(text);
        },
        success:function(text) {
          this.$message({
            message: text,
            type: 'success'
          });
        },
        warning:function(text) {
          this.$message({
            message: text,
            type: 'warning'
          });
        },
        //nav()
        navNubmer(parm){
          var url="/api/organization/employees/list/num";
          this.$http.get(url,{
            headers:{
              Authorization: 'bearer '+this.token
            },
            params:parm
          }).then(function(res){
                res=res.body;
                var data=[];
                //
            this.docterAndExpert[0].number=0;
            this.docterAndExpert[1].number=0;
                for(let i in res){
                  for(let k in this.docterAndExpert){
                       if(res[i].type==this.docterAndExpert[k].id){
                         if(res[i].type==1){
                           this.docterAndExpert[k].title="";
                         }
                         if(res[i].type==2){
                           this.docterAndExpert[k].title="";
                         }
                         this.docterAndExpert[k].number=res[i].userCount;
                       }
                  }
                }
          });
        }
      },
      created:function(){
         var parm=parseInt(this.$route.query.userType);
          if(parm){
          this.fromDat.userType=parm;
          if(parm==2 ){
            this.docterAndExpert=[
              {title:"",id:1,check:false,number:0},
              {title:"",id:2,check:true,number:0}
            ]
          }
          if(parm==1 ){
            this.docterAndExpert=[
              {title:"",id:1,check:true,number:0},
              {title:"",id:2,check:false,number:0}
            ]
          }
        }else {
            this.docterAndExpert=[
              {title:"",id:1,check:true,number:0},
              {title:"",id:2,check:false,number:0}
            ]
          }
        //nva
        this.navNubmer()
      },
      components: {
        CommonHeader: CommonHeader,Set:Set,RegionPicker:RegionPicker,//Vdistpicker:Vdistpicker
      },
    }
</script>



